.btn {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  width: 202px;
  padding: 1rem 2.5rem;
  font-family: "Gotu", serif;
  background-color: #408f95;
  font-size: 14px;
  color: #fff;
  border: 1px solid #408f95;
  text-align: center;
  position: relative;
  z-index: 1;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
}

.btn::before {
  position: absolute;
  right: -0.25rem;
  top: 0.25rem;
  width: 100%;
  height: 100%;
  border: 1px solid #408f95;
  transition: 0.3s;
  content: "";
  z-index: -1;
}

.btn::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.btn:hover {
  color: #408f95;
}

.btn:hover::after {
  transform: scale(1, 1);
}

.btn span::before,
.btn span::after {
  content: "";
  position: absolute;
  background-color: #fff;
  transition: 0.5s;
}

.btn span::before {
  top: calc(50% - 2px);
  right: 2.5rem;
  transform: translateY(calc(-50% - 1px)) rotate(37deg);
  width: 0.625rem;
  height: 0.0625rem;
}

.btn span::after {
  top: 50%;
  right: 2.5rem;
  transform: translateY(-50%);
  width: 2.5rem;
  height: 0.0625rem;
}

.btn:hover span::before,
.btn:hover span::after {
  background-color: #408f95;
}

